<template>
<div id="customer-detail">
<van-nav-bar
  :title="detailInfo.name"
  left-arrow
  class="customer-detail-nav"
  @click-left="leftClick"
  @click-right="rightClick"
>
<template #right>
	<i class="right-icon"><img src="~assets/images/common/icon/update.svg"></i>
</template>
</van-nav-bar>
	<div class="message-bxo" style="padding-left:5px;">
		<van-notice-bar
			left-icon="volume-o"
			:text="messageTitle"
		/>
	</div>
	<div class="customer-content">

		<!--货品信息-->
		<div class="customerinfo-box">
			<h5 class="box-title"><i class="customer-icon">单位信息</i><span class="customer-type">客户</span></h5>
			<ul class="ulinfo">
				<li>单位名称<span>{{detailInfo.corporatename}}</span></li>
				<li>性别<span>{{sexText}}</span></li>
				<li>单位类别<span>{{detailInfo.name}}</span></li>
				<li>地址<span>{{detailInfo.location}}</span></li>
			</ul>
		</div>
		<!--需求客户-->
		<div class="customer-box">
			<h5 class="box-title"><i class="customer-icon">通信信息</i></h5>
			<ul class="ulinfo">
				<li>默认联系人<span>{{detailInfo1.name}}</span></li>
				<li>联系电话<span>{{detailInfo1.telephone}}</span></li>
				<li>电子邮箱<span>{{detailInfo1.email}}</span></li>
				<li>网址<span>{{detailInfo1.website}}</span></li>
				<li>传真<span>{{detailInfo1.fax}}</span></li>
				<li>邮编<span>{{detailInfo1.postcode}}</span></li>
			</ul>
		</div>
		<!--详细弹窗-->
		<van-popup closeable v-model="detailShow"  :style="{ height:'55%',width:'80%',borderRadius:'6px'}">
			<div class="popup-box">
				<h2>{{selectedPN.title}}</h2>
				<div class="van-cell-group-box">
					<van-cell-group inset >
						<van-field  v-model="selectedPN.exclusiveprice" label="专属价(元)" @blur="updatExclusClick" />
					</van-cell-group>
						<div  class="vant-item-box">
							<span>造价(元):</span>
							<b>{{selectedPN.costs}}</b>
						</div>
						<div  class="vant-item-box">
							<span>重量(G)：</span>
							<b >{{selectedPN.weight}}</b>
						</div>
						<div  class="vant-item-box">
							<span>编码：</span>
							<b >{{selectedPN.encoded}}</b>
						</div>
						<div  class="vant-item-box">
							<span>库存(个)：</span>
							<b >{{selectedPN.stock}}</b>
						</div>
						<div  class="vant-item-box">
							<span>利润(元):</span>
							<b style="color:red;">{{(selectedPN.exclusiveprice-selectedPN.costs).toFixed(2)}}</b>
						</div>
					<p style="margin-top:20px;color:red;font-size:12px;">
						Tips：点击专属价格修改该产品对应客户的价格
					</p>
				</div>
			</div>
		</van-popup>
		<div class="productneed-box">
			<h5 class="box-title"><i class="customer-icon">产品需求</i><span class="customer-type addproductneed" @click="addProductNeed">添加需求</span></h5>
			<div class="need-box" v-if="detailInfo1.productneed">
				<div v-for="(item,index) in productNeedList" :key="index" class="need-item-box" @click="detlaiItemClick(item.id,index)">
					<span class="productneed-num">NO.{{index+1}}</span>
					<div class="productneed-info">
						<h4 class="productneed-name">{{item.title}}</h4>
						<div class="productneed-row">
							<p>产品单价：<span style="color:red;font-weight:600;">{{item.exclusiveprice}}</span></p>
							<p>产品单价：<span>{{item.costs}}</span></p>
							<p>产品规格：<span>{{item.specs}}</span></p>
						</div>
					</div>
				</div>
			</div>
		</div>
		
		<!--库存-->
		<div class="stock-box">
			<h5 class="box-title"><i class="customer-icon">库存信息</i></h5>
			<div class="box-info">
				备注：<i @click="deleteClick">删除</i>
			</div>
		</div>
		<!--以上大部分都可以抽成独立小组件-->
		<van-popup v-model="show" position="bottom"   round :style="{minHeight:'10%' }">
			<div class="select-box-item">
				<van-button type="primary"  color="white" style="{height:50px;}" @click="detailBottomClick"  block><span class="fontcolorb">详情</span></van-button>
				<van-button type="primary" color="white" @click="deleteBottomClick"  block><span class="fontcolorb">删除</span></van-button>
			</div>
		</van-popup>
	</div>
</div>
	
</template>

<script>
import { NavBar,Popup,Cell,Button,NoticeBar,SwipeCell,Field,CellGroup} from 'vant'
import {getCustomerDetail,deleteCustomer} from 'network/customer'
import {updateProductNeed,deleteProductNeed} from 'network/product'

export default {
  name: 'customerdetail',
	components:{
		[NavBar.name]:NavBar,
		[Popup.name]:Popup,
		[Cell.name]:Cell,
		[Button.name]:Button,
		[NoticeBar.name]:NoticeBar,
		[SwipeCell.name]:SwipeCell,
		[Field.name]:Field,
		[CellGroup.name]:CellGroup,
	},
	data(){
		return{
			detailInfo:'',				//客户的详细信息-(细节：把两数组分开一下)
			productNeedList:[],			//产品需求列表数组
			detailShow:false,			//详情框
			selectedPN:'',				//选中的那个数组的对象
			selectPNindex:'',			//选中数组那个下标	
			show:false,
			messageTitle:'浪花，从不伴随躲在避风港的小表演，而始终追赶着拚搏向前的巨轮。',
		}
	},
	methods:{	
		//这里需要获取详细商品的信息
		//返回上级
		leftClick(){
			this.$router.push('/customer').catch(err=>{err})
		},
		//添加产品需求
		addProductNeed(){
			//放入缓存
			localStorage.setItem("customerneedpid",this.detailInfo1.id);
			//console.log(id)
			//这个id刷新有问题 需要保存到缓存里面
			this.$router.push({name:'addproductneed',params:{id:this.detailInfo1.id}}).catch(res=>{res})
		},
		//产品需求详细点击
		detlaiItemClick(id,index){
			//显示弹出层
			//this.detailShow=true							//选中的那个ietm点击
			//弹出下面框选项
			this.show=true
			this.selectedPN=this.productNeedList[index]		//这个可以通过id找查找该对象
			this.selectPNindex=index						//储存点击的那个下标
		},
		//删除一个产品需求
		deDetailPNClick(){
			let selectPN=this.productNeedList[this.selectPNindex]
			//删除原来那个数组选中下标的那个数据
			//再删除数据库里的
			console.log(selectPN.id)
		},
		//底部按钮点击 详情-修改-删除
		detailBottomClick(){
			this.detailShow=true
			//先关闭底下弹窗
			this.show=false
		},
		updateBottomClick(){
			this.$toast('修改')
			//先关闭底下弹窗
			this.show=false
		},
		
		deleteBottomClick(){
			this.show=false				//显示询问弹窗
			let _self=this				//指针指向
			let selectedObj=this.productNeedList[this.selectPNindex]		//选中的那个对象
			//新建一个对象保存信息以便传到后台			//先准备一个后台需要的对象
			let proeductPNboj={
				customerid:selectedObj.customerid,					//客户的id
				productid:selectedObj.productid,					//该产品需求的id
				pnid:selectedObj.pnid,								//该订单的id
			}
			//console.log(proeductPNboj)
			
			this.$dialog.confirm({
				title: '警告',
				message:
					'请注意该操作不可恢复,是否继续删除该条数据?',
				})
				.then(() => {
					
					//删除数据库里的(数据库有必要给一个删除成功与否的标识)
					//先删除数据库里面的在删除数组的
					deleteProductNeed(proeductPNboj).then(res=>{
						setTimeout(()=>{
							//删除原来那个数组选中下标的那个数据
							_self.productNeedList.splice(_self.selectPNindex,1)
							_self.$toast(res)
						},800)
					})

					// on confirm
					//这里确定了就删除指定数组的下标数据
				})
				.catch(() => {
					// on cancel
					_self.$toast('取消')
			});
		},
		//弹窗专属价格修改
		updatExclusClick(){
			//修改this指向
			//1判断价格时候有改变
			//this.productNeedList[this.selectPNindex].exclusiveprice=100
			//产品需求的id,新价格,客户名称和客户的id
			let selectedObj=this.productNeedList[this.selectPNindex]								//选中的那个对象
			selectedObj.exclusiveprice=parseFloat(selectedObj.exclusiveprice).toFixed(2)			//取小数点2位数
			//先准备一个后台需要的对象
			let proeductPNboj={
				customerid:selectedObj.customerid,					//客户的id
				productid:selectedObj.productid,					//该产品需求的id
				exclusiveprice:selectedObj.exclusiveprice,			//新输入的价格
				pnid:selectedObj.pnid,								//该订单的id
			}
			
			//开始发送到数据库
			updateProductNeed(proeductPNboj).then(res=>{
				this.$toast(res)
			})
		},
		//修改用户
		rightClick(){
			//缓存写入一个修改的id
			localStorage.setItem('updateCustomerid',this.detailInfo.id)
			//跳转到修改页面
			this.$router.push('/updatecustomer').catch(res=>{res})
			return true
		},
		//删除信息
		deleteClick(){
			let id=this.detailInfo.id
			console.log(id+"先来一个弹窗再 确定再删除")
			let _self=this				//指针指向
			this.$dialog.confirm({
				title: '警告',
				message:
					'请注意该操作不可恢复,是否继续删除该条数据?',
				})
				.then(() => {
					//删除数据库里的(数据库有必要给一个删除成功与否的标识)
					//先删除数据库里面的在删除数组的
					deleteCustomer(id).then(res=>{
						setTimeout(()=>{
							_self.$toast(res)
							this.$router.go(-1)
						},800)
					})
					// on confirm
					//这里确定了就删除指定数组的下标数据
				})
				.catch(() => {
					// on cancel
					_self.$toast('取消')
			});
		}
	},
	computed:{
		detailInfo1(){
			return this.detailInfo
		},
		sexText(){
			if(this.detailInfo.sex==0){
				return '女'
			}
			return '男'
		}
	},
	activated(){
		//每次清空一下该数组保证数据不叠加
		this.productNeedList=[];
		//获取数据
		//let id=this.$route.params.id?this.$route.params.id:1;
		let id=localStorage.getItem("customerdetailid")?localStorage.getItem("customerdetailid"):1;		//获取上一个页面点击的缓存id
		//console.log(id)
		getCustomerDetail(id).then(res=>{
			this.detailInfo=res[0]											//客户的详细信息
			this.productNeedList.push(...res[0].productneed)				//客户产品需求
		})
	}
}
</script>

